import React, { useRef, useState } from 'react';

//icons (imported as svg using babel plugin)
import faBatteryFull from '../../icons/bat-charge.svg';
import faVolumeUp from '../../icons/volume-up.svg';
import faWifi from '../../icons/wifi.svg';
import faFeather from '../../icons/feather.svg';

//styles
import * as S from './styles';
import * as A from 'styles/shared-components';

//components
import Messages from 'components/Messages';
import AbelixApp from 'components/AbelixApp';
import DayNightSwitch from 'components/DayNightSwitch';
import MenuBar from 'components/MenuBar';
import Compose from 'components/Compose';
import ToggleCount from 'components/ToggleCount';
import DownloadButton from 'components/DownloadButton';
import AppStoreButton from 'components/AppStoreButton';
import PlayStoreButton from 'components/PlayStoreButton';
import Background from 'components/Background';
import Footer from 'components/Footer';

//hooks
import {
  useGoogleAnalytics,
  useHovered,
  useToggleBodyClass,
  useFindElementCenter,
  useMousePosition,
  useCanHover,
  useClock
} from 'utils/hooks';

import useIntroAnimation from './use-intro-animation';

import 'focus-visible';
import DownloadModal from '../DownloadModal';
import { useBoolean } from 'react-hanger';
import {IphoneBox} from "./styles";
import Flex from "../../styles/flex";

//env
const { REACT_APP_ANALYTICS_ID, REACT_APP_DOWNLOAD_LINK } = process.env;

const redirectDownload = () => {
  if (window.location.href.includes('get-app')) {
    window.location.replace(REACT_APP_DOWNLOAD_LINK);
  }
};

function Home({ isAnimationDone, night }) {
  redirectDownload();

  const [composeIsOpen, setComposeOpen] = useState(false);
  const [toggleCount, setToggleCount] = useState(0);

  const [text, setText] = useState(``);

  // refs
  const contentRef = useRef();
  const messagesWindowRef = useRef();

  //custom hooks
  const isInSizzy = window.__sizzy;
  const { fabPose, menuBarPose, messagesPose, homePose } = useIntroAnimation(!isInSizzy, isAnimationDone);
  const canHover = useCanHover();
  const isHoveringMessages = useHovered();
  const isHoveringCompose = useHovered();
  const windowCenter = useFindElementCenter(messagesWindowRef);
  const { y: mouseY } = useMousePosition(isHoveringCompose.value);
  const clock = useClock();
  const showModal = useBoolean(false);

  // side effects
  useGoogleAnalytics(REACT_APP_ANALYTICS_ID, isAnimationDone.value);
  // useToggleBodyClass(isAnimationDone.value, ['scroll', 'no-scroll']);

  // computed
  const isNotHoveringMenuBar = mouseY === null || mouseY >= 25;
  const showComposeWindow = composeIsOpen || (isHoveringCompose.value && isNotHoveringMenuBar);
  const isBig = window.innerWidth > 450;

  // methods
  const onToggleNight = () => {
    night.toggle();
    setToggleCount(toggleCount + 1);
  };

  return (
    <S.Home>
      {showModal.value && <DownloadModal onClose={showModal.setFalse} />}
      <S.MainSection>
        <Background night={night.value} startLoadingLight={isAnimationDone.value} show={isBig} />

        <MenuBar
          className="menubar"
          pose={menuBarPose}
          selected={showComposeWindow}
          onClick={() => setComposeOpen(v => !v)}
          mainIcon={faFeather}
          icons={[faWifi, clock, faVolumeUp, '100%', faBatteryFull]}
        />

        <Compose
          {...isHoveringCompose.bind}
          text={text}
          setText={setText}
          setComposeOpen={setComposeOpen}
          composeIsOpen={composeIsOpen}
          visible={showComposeWindow}
        />

        <S.Content ref={contentRef}>
          <S.WindowBox ref={messagesWindowRef} initialPose="hidden" pose={homePose} {...windowCenter}>
            <AbelixApp/>
          </S.WindowBox>

          <A.Space huge />

          <S.TextContent isAnimationDone={isAnimationDone.value} pose={homePose}>
            <S.Title> Добро пожаловать в Abelix! </S.Title>
            <br/>
            <S.Text>
              Ваш путь к выгодным покупкам и незабываемым вознаграждениям!
            </S.Text>
            <br/>
            <S.Text>
              <span>
                <A.Hover {...isHoveringMessages.bind}>Abelix</A.Hover> - это ваш личный путеводитель в мире лучших брендов и заведений. У нас собраны ваши любимые места для шопинга, развлечений и отдыха, предоставляющие уникальные возможности для накопления бонусов.
                <br/>
                <br/>
              </span>
              <>
                <S.Subtitle>
                  Получайте кэшбек за каждую покупку:
                </S.Subtitle>
                <S.Text>
                  С каждой совершенной сделкой через <A.Hover {...isHoveringMessages.bind}>Abelix</A.Hover> вы накапливаете кэшбек. Чем больше вы делаете покупок — тем выше процент вашего вознаграждения! Это ваш шанс делать покупки не только выгодно, но и увлекательно.
                </S.Text>
                <br/>
              </>
              <>
                <S.Subtitle>
                  Уникальные акции и розыгрыши:
                </S.Subtitle>
                <S.Text>
                  Помимо кэшбека, <A.Hover {...isHoveringMessages.bind}>Abelix</A.Hover> приглашает вас в увлекательный мир акций и розыгрышей от вашего любимого бренда или заведения. Участвуйте и выигрывайте эксклюзивные призы!
                </S.Text>
                <br/>
              </>
              <>
                <S.Subtitle>
                  Эксклюзивные преимущества для лояльных клиентов:
                </S.Subtitle>
                <S.Text>
                  Мы ценим вашу верность. Поэтому чем чаще вы совершаете покупки через наше приложение, тем больше бонусов и уникальных предложений мы предоставляем вам.
                </S.Text>
                <br/>
              </>
              <>
                <S.Subtitle>
                  Легкость и удобство в использовании:
                </S.Subtitle>
                <S.Text>
                  <A.Hover {...isHoveringMessages.bind}>Abelix</A.Hover> обеспечивает простоту и безопасность оплаты. Привязывайте свои карты, делайте покупки в любимых местах, получайте кэшбек и наслаждайтесь выгодами легко и быстро.
                </S.Text>
                <br/>
              </>
              <>
                <S.Text>
                  Присоединяйтесь к нам сегодня и откройте для себя новый уровень покупок! <A.Hover {...isHoveringMessages.bind}>Abelix</A.Hover> - это не просто покупки, это новый уровень удовольствия от каждой сделки.
                </S.Text>
                <br/>
              </>

              <br />
              <span>Скачать приложения</span>
            </S.Text>

            <A.Space />

            <div style={{display: 'flex', justifyContent: 'left'}}>
              <AppStoreButton startLoading={isAnimationDone.value} />
              <PlayStoreButton startLoading={isAnimationDone.value} />
            </div>

            <A.Space />

            <S.Platforms>Доступно для IOS и Android</S.Platforms>

            <A.Space />

            <DayNightSwitch value={night.value} onChange={onToggleNight} />
            {/*<ToggleCount onTweet={tweetProgress} count={toggleCount} />*/}
          </S.TextContent>
        </S.Content>
        <div style={{display: 'flex'}}>
          <S.Content>
            <S.TextContent>
              <S.Title> Контакты </S.Title>
              <br />
              <S.Text>
                <A.Hover {...isHoveringMessages.bind}>Адрес:</A.Hover> г. Алматы, ул. Байзакова, 280, Smart Point
              </S.Text>
              <br />
              <S.Text>
                <A.Hover {...isHoveringMessages.bind}>Телефон:</A.Hover> +7 (706) 425-00-30
              </S.Text>
            </S.TextContent>
          </S.Content>
          <S.Content>
            <S.TextContent>
              <S.Title> Реквизиты компании </S.Title>
              <br />
              <S.Text>
                <A.Hover {...isHoveringMessages.bind}>Название:</A.Hover> ТОО «Abelix»
              </S.Text>
              <br />
              <S.Text>
                <A.Hover {...isHoveringMessages.bind}>БИН:</A.Hover> 230340011538
              </S.Text>
              <br />
              <S.Text>
                <A.Hover {...isHoveringMessages.bind}>Номер счета:</A.Hover> KZ81601A861014458911
              </S.Text>
              <br />
              <S.Text>
                <A.Hover {...isHoveringMessages.bind}>Юр. Адрес:</A.Hover> Казахстан г. Алматы, ул. Байзакова, 280, индекс: 050040
              </S.Text>
            </S.TextContent>
          </S.Content>
        </div>

      </S.MainSection>
      <Footer composeIsOpen={composeIsOpen} menuBarPose={menuBarPose} />
    </S.Home>
  );
}

export default Home;
