import React from 'react';

//styles
import * as S from './styles';

function AppStoreButton({ onClick }) {
  return (
    <S.Button
      onClick={onClick}
      role="button"
      tabIndex={0}
      onKeyPress={e => {
        if ((e.which === 13 || e.which === 32) && onClick) {
          onClick(e);
        }
      }}
    >

    </S.Button>
  );
}

export default AppStoreButton;
